.modal-open {
  overflow: hidden;
}

footer {
  background-color: #f7f7f7;
}

.footer-text-cont {
  font-size: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 29px;
  white-space: nowrap;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    flex-direction: row;
  }
}

.footer-text-p1 {
  text-align: center;

  @media screen and (max-width: 320px) {
    margin-bottom: 12px;
  }
}

.footer-text-p2 {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.icon-heart {
  fill: #b92f2c;
  margin-left: 5px;
  margin-right: 5px;
}

.team-modal-link {
  margin-left: 5px;
  text-decoration: underline;
}

.team-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: auto;
}

.team-modal {
  position: fixed;
  top: 20px;
  bottom: 20px;
  max-height: 100vh;
  overflow: auto;
  left: 50%;
  transform: translate(-50%);
  width: 270px;
  background-color: #f0f0f0;
  border-radius: 5px;
  padding: 20px;

  @media screen and (min-width: 768px) {
    width: 708px;
  }

  @media screen and (min-width: 1280px) {
    width: 806px;
  }
}

.modal-close-btn {
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px;
}

.modal-title {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  color: #545454;
}

.footer-team-members {
  color: #545454;
  display: grid;
  grid-template-columns: 1fr;

  @media screen and (min-width: 768px) {
    isplay: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  @media screen and (min-width: 1280px) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.member {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: transform 0.2s ease-in-out;

  &:hover {
    transform: scale(1.1);
  }
}

.team-image {
  border-radius: 50%;
}

.member-info {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.teammate {
  margin-bottom: 5px;
  font-weight: 700;
}

.team-role {
  font-style: italic;
}
