.gallery {
  max-width: 280px;
  grid-column-gap: 16px;
  grid-row-gap: 32px;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  padding-bottom: 20px;
  list-style: none;
  display: flex;
}

@media screen and (min-width: 768px) {
  .gallery {
    max-width: 768px;
    grid-column-gap: 32px;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery {
    max-width: 1280px;
    grid-column-gap: 18px;
    justify-content: left;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

.gallery__item {
  width: calc(100% - 1px);
  break-inside: avoid;
  cursor: pointer;
  border-radius: 10px;
  flex-direction: column;
  transition: all .2s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
  box-shadow: 0 5px 6px 1px rgba(73, 85, 94, .863);
}

@media screen and (min-width: 768px) {
  .gallery__item {
    width: calc(50% - 30px);
  }
}

@media screen and (min-width: 1280px) {
  .gallery__item {
    width: calc(33.3333% - 13.3333px);
  }
}

.gallery__item:hover {
  transition: all .2s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.03)translateZ(0);
  box-shadow: 0 1px 1px rgba(185, 46, 44, .69), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(185, 46, 44, .592);
}

.gallery__wrap {
  padding: 10px 10px 0 3px;
}

.gallery__img {
  border-radius: inherit;
  max-width: 100%;
  height: auto;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  flex: 1;
  display: block;
  box-shadow: 0 3px 4px 1px rgba(73, 85, 94, .17);
}

.gallery__title {
  text-transform: uppercase;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #000;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 800;
